<h1 id="reactive-spring" tabindex="-1"><a class="header-anchor" href="#reactive-spring" aria-hidden="true">#</a> Reactive Spring</h1>
<p>反应式编程（Reactive Programming）是一种面向数据流和变化传播的编程范式。</p>
<p>实际项目中，线程阻塞场景往往不只有Http请求阻塞，还有Mysql请求，Redis请求，Kafka请求等等导致的阻塞。从这些数据源中获取数据时，大多数都是阻塞直到数据源返回数据。
而Reactive Spring强大在于，它也支持这些数据源的非阻塞响应式编程。</p>
